import sanitizeHtml from "sanitize-html";
export const sanitize = (dirty) => {
    return sanitizeHtml(dirty, {
        allowTags: ["b", "i", "em", "strong", "a", "span"],
        allowAttributes: {
            a: ["href"]
        }
    });
};



